import qs from 'qs';
/**
 * 数据类型
 */
export const application = {
    json: 'application/json',
    form: 'application/x-www-form-urlencoded',
};
/**
 * 根据数据类型生成方法
 */
const applicationToBodyFun = {
    [application.json]: JSON.stringify,
    [application.form]: qs.stringify,
};
/**
 * data 转为请求主体
 */
const toBody = (config) => {
    if (config.method === 'GET') {
        const body = qs.stringify(config.data);
        if (body)
            config.url += `?${body}`;
    }
    else {
        const contentType = config.headers && config.headers['Content-type'];
        if (contentType && applicationToBodyFun[contentType]) {
            config.body = applicationToBodyFun[contentType](config.data);
        }
    }
    return config;
};
/**
 * 如果配置为文本类型，直接写入 label
 */
const labelToConfig = (config) => (typeof config === 'string' ? { label: config } : config);
/**
 * 对象数据写入表单对象
 * 主要用于上传文件
 */
const getFormData = (data) => {
    const body = new FormData();
    const setData = (data, key = '') => {
        Object.keys(data).forEach(i => {
            const item = data[i];
            if (item && typeof item === 'object' && !(item instanceof File)) {
                setData(item, key ? `${key}[${i}]` : i);
            }
            else {
                body.append(key ? `${key}[${i}]` : i, item);
            }
        });
    };
    setData(data);
    return body;
};
/**
 * 统计时间
 */
const statisticalTime = () => {
    const start = new Date();
    return () => {
        const end = new Date();
        return {
            start: start.toTimeString(),
            end: end.toTimeString(),
            total: `${(+end - +start) / 1000}秒`,
        };
    };
};
/**
 * 错误解析字段
 */
const erroText = {
    timeout: '网络连接超时',
    'Network Error': '请求地址错误或跨域未允许',
    'Failed to fetch': '请求地址错误或跨域未允许',
};
/**
 * 异常分析 错误信息 => 错误解析文本
 */
const erroToText = (error) => {
    for (const reg in erroText) {
        if (new RegExp(reg).test(error)) {
            return erroText[reg];
        }
    }
    return '其他错误';
};
/**
 * 控制台打印颜色
 */
const consoleStyle = {
    request: 'color: #0089E5;',
    success: 'color: #2DB700;',
    fail: 'color: #F41900;',
};
/**
 * 打印日志
 * 请求配置的 请求标签 label 代表打印日志的 文本内容
 */
export const log = {
    request: (config) => {
        console.groupCollapsed(`%cRequest: ${config.label || config.url} ⇅`, consoleStyle.request);
        console.log('请求类型：', config.method);
        console.log('请求地址：', config.url);
        console.log('请求数据：', config.data || '无');
        console.log('请求配置：', config);
        console.groupEnd();
    },
    response: (res, config, success) => {
        let title = `%cResponse: ${config.label || config.url} ${success ? '√' : '×'}`;
        if (res.time && res.time.total)
            title += ` 用时：${res.time.total}`;
        console.groupCollapsed(title, consoleStyle[success ? 'success' : 'fail']);
        console.log('响应数据：', res);
        if (!success) {
            console.log('响应异常：', res.error || '无');
            console.log('异常解析：', res.errorText);
        }
        console.groupEnd();
    },
};
/**
 * 请求模型
 */
export default class FetchRequest {
    constructor(config) {
        this.host = '';
        this.apiPath = '';
        this.defaultConfig = {
            mode: 'cors',
            method: 'GET',
            cache: 'default',
            credentials: 'omit',
            responseType: 'json',
            headers: {
                Accept: application.json,
                'Content-type': application.json,
            },
            timeout: 5000,
        };
        this.interceptorsRequest = (config) => config;
        this.interceptorsResponse = (res, _config) => res;
        /**
         * 执行请求
         */
        this.request = (configs) => {
            let { url = '', ...config } = configs;
            // 拼接地址
            if (!/^http/.test(url))
                url = this.baseURL + url;
            // 请求拦截
            config = this.interceptorsRequest({ url, ...this.defaultConfig, ...config });
            // 转为主体
            config = toBody(config);
            // 开始统计时间
            const st = statisticalTime();
            // 发出请求
            return Promise.race([
                fetch(config.url, config),
                new Promise((_, reject) => setTimeout(reject, config.timeout, 'request timeout')),
            ])
                .then(response => {
                if (response instanceof Response) {
                    switch (config.responseType) {
                        case 'text':
                            return { text: response.text() };
                            break;
                        case 'blob':
                            return { blob: response.blob() };
                            break;
                        default:
                            return response.json();
                            break;
                    }
                }
                return response;
            }) // 转化响应数据
                .catch(error => ({
                error,
                errorText: erroToText(error),
            }))
                .then(res => this.interceptorsResponse({ time: st(), ...res }, config)); // 载入响应拦截
        };
        this.createRequest = (method) => (url, data, ...args) => this.request(Object.assign({ method, url, data }, ...args.map(i => labelToConfig(i))));
        this.get = this.createRequest('GET');
        this.post = this.createRequest('POST');
        this.put = this.createRequest('PUT');
        this.patch = this.createRequest('PATCH');
        this.del = this.createRequest('DELETE');
        this.upload = (url, data, ...args) => this.request(Object.assign({ method: 'POST', headers: {}, url, data, body: getFormData(data) }, ...args.map(i => labelToConfig(i))));
        if (config) {
            const { defaultConfig, ...configs } = config;
            Object.assign(this.defaultConfig, defaultConfig);
            Object.assign(this, configs);
        }
    }
    /**
     * 基础路径
     */
    get baseURL() {
        return this.host + this.apiPath;
    }
}
export const { get, post, put, patch, del, upload } = new FetchRequest();
