import React from 'react';
import { getImg } from '@dyb881/file';
export default class Img extends React.Component {
    constructor() {
        super(...arguments);
        this.state = {
            src: '',
            openMode: false,
            modeStyle: {},
            loading: false,
            show: false,
            err: false,
        };
        this.load = () => this.loadImg().catch(() => {
            this.setState({ err: true });
        });
        this.loadImg = async () => {
            this.setState({ loading: true, err: false });
            const { src, mode = 'none' } = this.props;
            const { openMode } = this.state;
            const { src: imgSrc, width, height } = await getImg(src);
            const modeStyle = {};
            if (openMode) {
                const { clientWidth, clientHeight } = this.box;
                const widthRatio = width / clientWidth;
                const heightRatio = height / clientHeight;
                if (mode.includes('aspectFit')) {
                    modeStyle[widthRatio > heightRatio ? 'width' : 'height'] = '100%';
                }
                if (mode.includes('aspectFill')) {
                    modeStyle[widthRatio < heightRatio ? 'width' : 'height'] = '100%';
                }
            }
            this.setState({
                loading: false,
                src: imgSrc,
                modeStyle,
            });
        };
    }
    componentDidMount() {
        const { clientWidth, clientHeight } = this.box;
        this.setState({ openMode: !!(clientWidth && clientHeight), show: true }, () => {
            const { src, show = true } = this.props;
            src && show && this.load();
        });
    }
    getSnapshotBeforeUpdate(prevProps) {
        const { src, show = true } = this.props;
        const { src: pSrc, show: pShow = true } = prevProps;
        return {
            isNewSrc: src !== pSrc,
            isToShow: show && !pShow,
        };
    }
    componentDidUpdate(_prevProps, _prevState, update) {
        const { isNewSrc, isToShow } = update;
        if (isNewSrc || isToShow) {
            this.load(); // 更新或展示图片
        }
    }
    render() {
        const { className, style, mode = 'none', loadedTip = '加载中', reloadTip = '重新加载', src: _src, show: _show, ...props } = this.props;
        const { src, openMode, loading, show, modeStyle, err } = this.state;
        const boxClassAndStyle = { className: ['dyb-img', className].join(' '), style };
        // 使用空元素确定组件尺寸
        if (!show)
            return React.createElement("div", Object.assign({}, boxClassAndStyle, { ref: box => this.box || (this.box = box) }));
        // 不开启裁剪、缩放模式
        if (!openMode || mode === 'none') {
            return loading ? loadedTip : React.createElement("img", Object.assign({}, boxClassAndStyle, { src: src, alt: "" }, props));
        }
        const modeClass = !Array.isArray(mode) ? `dyb-img-mode-${mode}` : mode.map(i => `dyb-img-mode-${i}`).join(' ');
        boxClassAndStyle.className += ` dyb-img-box ${modeClass}`;
        let res = React.createElement("img", { src: src, style: modeStyle, alt: "" });
        if (loading)
            res = React.createElement("div", { className: "dyb-img-loading" }, loadedTip);
        if (err)
            res = (React.createElement("div", { className: "dyb-img-reload", onClick: this.load }, reloadTip));
        return (React.createElement("div", Object.assign({}, boxClassAndStyle, props), res));
    }
}
