/**
 * json字符串 转 js对象
 */
export const jsonParse = (text, defaults, ...args) => {
    if (!defaults)
        defaults = {};
    try {
        return JSON.parse(text, ...args) || defaults;
    }
    catch (e) {
        return defaults;
    }
};
/**
 * js对象 转 json字符串
 */
export const jsonStringify = (data, defaults = '{}', ...args) => {
    try {
        return JSON.stringify(data, ...args) || defaults;
    }
    catch (e) {
        return defaults;
    }
};
